<template>
  <div class="set1">
    <div class="setwrapper">
      <div class="setinner">
        <Row>
          <Col span="8"  class-name="settitle">
            显示设置
          </Col>
        </Row>
        <Row>
          <Col offset="6" span="12" class-name="setset">
          <i-switch v-model="hasnumber"></i-switch>
          <span>问卷显示问题编号</span>
          </Col>
        </Row>
        <Row>
          <Col span="8" class-name="settitle">
            回收设置
          </Col>
        </Row>
        <Row>
          <Col offset="6" span="12" class-name="setset">
          <i-switch v-model="issetddl"></i-switch>
          <span>设定回收结束时间</span>
          </Col>
        </Row>
        <transition name="fade">
        <Row v-if="issetddl">
          <Col offset="6" span="18" class-name="setset">
            <span style="width:48px;height:24px;display:inline-block"></span>
            <span>
              <!-- {{recoveryat}} -->
              <DatePicker  v-model="recoveryat" type="datetime" format="yyyy-MM-dd" placeholder="选择日期和时间"></DatePicker>
            </span>
          </Col>
        </Row>
        </transition>
        <Row>
          <Col offset="6" span="12" class-name="setset">
          <i-switch v-model="ischecked" @on-change="clear()"></i-switch>
          <span>答题需要登陆验证</span>
          </Col>
        </Row>
        <Row v-if = "ischecked">
          <Col offset="6" span="12" class-name="setset">
          <i-switch v-model="onceanswer"></i-switch>
          <span>用户只能回答一次</span>
          </Col>
        </Row>
<!--         <Row>
          <Col span="8"  class-name="settitle">
            协同编辑
          </Col>
        </Row>
        <Row>
          <Col offset="6" span="14" class-name="setset">
          <span>添加其他人员编辑此问卷</span>
          </Col>
        </Row>
 --><!--         <Row>
          <Col offset="6" span="18" class-name="setset">
            <span style="width:18px;height:24px;display:inline-block"></span>
              <Avatar icon="person" size="small" />
              <span class="creator">{{creater}}</span>
              <span class="creator11">创建者</span>
          </Col>
        </Row>
        <Row>
          <Col offset="6" span="18" class-name="setset">
            <span style="width:18px;height:24px;display:inline-block"></span>
            <span  class="creator">
              <Input placeholder="天外天账号" style="width:120px" size="small" ></Input>
            </span>
            <span class="creator11 addworker">添加</span>
          </Col>
        </Row> -->
        <Row>
          <Col offset="8" span="8" style="text-align:center;margin-top:20px">
          <div @click="clicksave">
            <Button type="primary"style="width:80px">确定</Button>
          </div>
          </Col>
        </Row>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data () {
    return {
      issetddl: false,
      hasnumber: false,
      recoveryat: '',
      ischecked: false,
      onceanswer: false,
      shareid: ''
    }
  },
  methods: {
    clear() {
      this.onceanswer = false
    },
    clicksave () {
      console.log(this.recoveryat)
      var uphasnumber = this.hasnumber
      var upischecked = this.ischecked
      var uponceanswer = this.onceanswer
      if (this.issetddl)
        var upissetddl = 1
      else
        var upissetddl = 0
      if (this.recoveryat === '')
        var uprecoveryat = ''
      else
        var uprecoveryat = Math.floor(this.recoveryat.getTime())
      // var uprecoveryat = this.recoveryat
      console.log(uprecoveryat)
      if (uphasnumber === false) {
        uphasnumber = 0
      } else {
        uphasnumber = 1
      }
      if (upischecked === false) {
        upischecked = 0
      } else {
        upischecked = 1
      }
      if (uponceanswer === false) {
        uponceanswer = 0
      } else {
        uponceanswer = 1
      }
      var id = this.$store.getters.getid
      // console.log(id)
      // console.log({ hasnumber: uphasnumber, recoveryat: uprecoveryat, ischecked: upischecked, onceanswer: uponceanswer, issetddl: upissetddl })
      this.$http.post('minequestion/install/' + id, { hasnumber: uphasnumber, recoveryat: uprecoveryat, ischecked: upischecked, onceanswer: uponceanswer, issetddl: upissetddl }).then((response) => {
        console.log(response.body)
        this.$store.commit('savetopic', response.body.questionnaire_data.name)
        this.$store.commit('savecreater', response.body.questionnaire_data.twt_name)
        this.$Message.success('已保存设置')
      })
    }
  },
  computed: {
    creater () {
      return this.$store.getters.getcreater
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.$http.get('loginStatus').then(response => {
        if (response.body.status === 0) window.location.href="http://survey.twtstudio.com/api/login"
        else {
          vm.$http.get('minequestion/install/' + vm.$route.params.id).then((response) => {
            // console.log(response.body)
            // console.log(response.body.questionnaire_data.recovery_at)
            // console.log(typeof(response.body.questionnaire_data.recovery_at))
            if (response.body.questionnaire_data.issetddl === 1)
              vm.issetddl = true
            else
              vm.issetddl = false
            if (response.body.questionnaire_data.hasnumber === 1)
              vm.hasnumber = true
            else
              vm.hasnumber = false
            if (response.body.questionnaire_data.ischecked === 1)
              vm.ischecked = true
            else
              vm.ischecked = false
            if (response.body.questionnaire_data.onceanswer === 1)
              vm.onceanswer = true
            else
            vm.onceanswer = false
            vm.recoveryat = response.body. questionnaire_data.recovery_at
            vm.$store.commit('saveid', vm.$route.params.id)
          })
        }
      })
    })
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.fade-enter-active, .fade-leave-active
  transition opacity .5s
.fade-enter, .fade-leave-to
  opacity 0
.set1
  height 100%
  background-color rgba(233,234,238,0.52)
  position relative
  top 130px
  padding-top 25px
.setwrapper
  max-width 1095px
  height 100%
  margin 0 auto
  padding-top 35px
  padding-bottom 100px
  background-color white
.setinner
  width 500px
  margin 0 auto
.settitle
  font-size 16px
  margin 10px auto
.setset span
  margin-left 14px
.setset
  font-size 14px
  margin-bottom 10px
.creator
  position relative
  bottom 7px
  color #00a1e9
.creator11
  position relative
  bottom 6px
  color #707070
.addworker
  color #00a1e9
  cursor pointer
</style>



